import React from "react";
import "./Greeting.css";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import { greeting, skillsSection } from "../../portfolio";
import { Fade } from "react-reveal";
import emoji from "react-easy-emoji";
import Pdf from "../../assests/documents/SrisaiNachuriResumeMay2020.pdf";
import SoftwareSkill from "../../components/softwareSkills/SoftwareSkill";

export default function Greeting() {
    return (
        <Fade bottom duration={1000} distance="40px">
            <div className="greet-main" id="greeting">
                <div className="greeting-main">
                    <div className="greeting-text-div">
                        <div>
                            <h1 className="greeting-text"> {greeting.title}</h1>
                            <p className="greeting-text-p">
                                {greeting.subTitle.map((sub) => {
                                    return (
                                        <div className="sub">
                                            <b>{sub}</b>
                                        </div>
                                    );
                                })}
                            </p>
                            <SocialMedia />

                            <div className="button-greeting-div">
                                <a href={"#contact"}>
                                    <i
                                        class="fas fa-arrow-down"
                                        style={{
                                            fontSize: "2.5em",
                                            textAlign: "center",
                                            color: "#4747d1",
                                        }}
                                    ></i>
                                </a>

                                {/*<Button text="About me" href="#contact" />*/}
                            </div>
                        </div>
                    </div>
                    <div>
                        <div>
                            <SoftwareSkill />
                        </div>
                    </div>
                    {/*
                    <div className="greeting-image-div">
                        <img
                            alt="saad sitting on table"
                            src={require("../../assests/images/manOnTable.svg")}
                        ></img>
                    </div>*/}
                </div>
            </div>
        </Fade>
    );
}
